import { ReactNode, useContext } from 'react';
import { StaticAssetContext } from './StaticAssetContext';

interface StaticAssetProps {
    path: string;
    children: (result: { path: string }) => ReactNode;
}

export default function StaticAsset({ path, children }: StaticAssetProps) {
    const { assetPrefix } = useContext(StaticAssetContext);

    const newProps = {
        path: `${assetPrefix ? assetPrefix : ''}${path}`,
    };

    return children(newProps);
}
